@import '../common/variables.scss';
.hours {
  display: none;
}

@media (min-width: $tablet-width) {
  .hours {
    display: flex;
    background-color: #fff;
    justify-content: center;
  }

  .hours__wrapper {
    width: 94%;
    display: flex;
    justify-content: space-between;
    min-height: 320px;
    padding: 10% 0;
  }

  .hours__img {
    width: 45%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .hours__text {
    font-size: 30px;
    line-height: 1.2;
    width: 50%;

    p {
      font-size: 14px;
    }

    a {
      font-size: 14px;
      color: #dd5d80;
    }
  }
}

@media (min-width: $desktop-width) {

  .hours {
    background-color: #f2f2f2;
  }

  .hours__wrapper {
    width: 80%;
  }

  .hours__text {
    br {
      display: none;
    }

    p {
      font-size: 18px;
      line-height: 30px;
    }

    a {
      font-size: 18px;
      line-height: 30px;
    }
  }

  .hours__img {
    img {
      width: 300px;
      height: 345px;
    }
  }
}